import { React, useState } from "react";
import * as s from "../styles/globalStyles";
import ScrollableAnchor from "react-scrollable-anchor";
import { Parallax, Background } from "react-parallax";
import treebg from "../_resources/trees.jpg";
import roadmapbg from "../_resources/roadmap.jpg";

function RoadmapS() {
  return (
    <ScrollableAnchor id={"roadmap"}>
      {/* <Roadmap></Roadmap> */}
      <Parallax bgImage={treebg} strength={500}>
        <s.StickyBarSpacer> </s.StickyBarSpacer>

        <s.ParallaxContainer>
          <s.ParallaxTextContainer>
            <s.FullArticleImage src={roadmapbg}></s.FullArticleImage>
            <s.TextTitle> Road Map </s.TextTitle>
            <s.SpacerMedium></s.SpacerMedium>
            <s.TextSubTitle>Phase 1</s.TextSubTitle>
            <s.SpacerMedium></s.SpacerMedium>

            <s.UnorderedList>
              <s.ListItem>Pre sale and public sale will take place</s.ListItem>
              <s.ListItem>
                Listing on Opensea and rarity checker sites
              </s.ListItem>

              <s.ListItem>
                Legendary Dragons will be raffled off to holders who have their
                dragon delisted
              </s.ListItem>

              <s.ListItem>Setting up of community wallet</s.ListItem>
            </s.UnorderedList>

            <s.SpacerMedium></s.SpacerMedium>
            <s.SpacerMedium></s.SpacerMedium>

            <s.TextSubTitle>Phase 2</s.TextSubTitle>
            <s.SpacerMedium></s.SpacerMedium>

            <s.UnorderedList>
              <s.ListItem>
                Holders will be able to participate in our collect to earn
                Dragonball Z inspired game.
              </s.ListItem>

              <s.ListItem>
                Airdrops, giveaways and exclusive perks will be given to holders{" "}
              </s.ListItem>
            </s.UnorderedList>

            <s.SpacerMedium></s.SpacerMedium>
            <s.SpacerMedium></s.SpacerMedium>

            <s.TextSubTitle>Phase 3</s.TextSubTitle>
            <s.SpacerMedium></s.SpacerMedium>

            <s.UnorderedList>
              <s.ListItem>Metaverse land purchase and development </s.ListItem>
              <s.ListItem> Tokens will be implemented </s.ListItem>
              <s.ListItem>Baby dragons will be released !</s.ListItem>
            </s.UnorderedList>

            <s.SpacerMedium></s.SpacerMedium>
            <s.SpacerMedium></s.SpacerMedium>

            <s.TextSubTitle>Phase 4</s.TextSubTitle>
            <s.SpacerMedium></s.SpacerMedium>

            <s.UnorderedList>
              <s.ListItem>Dragon racing game release</s.ListItem>
              <s.ListItem>Release of Roadmap 2.0</s.ListItem>
            </s.UnorderedList>

            <s.SpacerMedium></s.SpacerMedium>
            <s.SpacerMedium></s.SpacerMedium>
          </s.ParallaxTextContainer>

          <s.SpacerMax></s.SpacerMax>
        </s.ParallaxContainer>
        <s.StickyBarSpacer> </s.StickyBarSpacer>
      </Parallax>
    </ScrollableAnchor>
  );
}

export { RoadmapS };
