import { React, useState } from "react";
import * as s from "../styles/globalStyles";
import ScrollableAnchor from "react-scrollable-anchor";
import d1 from "../_resources/collections/2164.png";
import d2 from "../_resources/collections/2312.png";
import d3 from "../_resources/collections/2587.png";
import d4 from "../_resources/collections/2590.png";
import d5 from "../_resources/collections/2882.png";
import d6 from "../_resources/collections/3257.png";
import d7 from "../_resources/collections/3406.png";
import d8 from "../_resources/collections/3823.png";
import { useMediaQuery } from "react-responsive";

function CollectionsSection() {
  const isBigScreen = useMediaQuery({ query: "(min-width: 1824px)" });
  const isTabletOrMobile = useMediaQuery({ query: "(max-width: 1224px)" });
  const isPortrait = useMediaQuery({ query: "(orientation: portrait)" });
  const isRetina = useMediaQuery({ query: "(min-resolution: 2dppx)" });

  return (
    <ScrollableAnchor id={"collections"}>
      <s.SubContainer>
        <s.StickyBarSpacer> </s.StickyBarSpacer>
        {/* <MintComponent></MintComponent> */}
        <s.TextTitle>Collections</s.TextTitle>

        {isTabletOrMobile ? (
          // Mobile
          <s.SubHContainer>
            <s.MobileCollectionCards>
              <s.MobileCollectionImage src={d1}></s.MobileCollectionImage>
            </s.MobileCollectionCards>

            <s.MobileCollectionCards>
              <s.MobileCollectionImage src={d2}></s.MobileCollectionImage>
            </s.MobileCollectionCards>

            <s.MobileCollectionCards>
              <s.MobileCollectionImage src={d3}></s.MobileCollectionImage>
            </s.MobileCollectionCards>

            <s.MobileCollectionCards>
              <s.MobileCollectionImage src={d4}></s.MobileCollectionImage>
            </s.MobileCollectionCards>

            <s.MobileCollectionCards>
              <s.MobileCollectionImage src={d5}></s.MobileCollectionImage>
            </s.MobileCollectionCards>

            <s.MobileCollectionCards>
              <s.MobileCollectionImage src={d6}></s.MobileCollectionImage>
            </s.MobileCollectionCards>

            <s.MobileCollectionCards>
              <s.MobileCollectionImage src={d7}></s.MobileCollectionImage>
            </s.MobileCollectionCards>

            <s.MobileCollectionCards>
              <s.MobileCollectionImage src={d8}></s.MobileCollectionImage>
            </s.MobileCollectionCards>
          </s.SubHContainer>
        ) : (
          //  Desktop
          <s.SubHContainer>
            <s.CollectionCards>
              <s.CollectionImage src={d1}></s.CollectionImage>
            </s.CollectionCards>

            <s.CollectionCards>
              <s.CollectionImage src={d2}></s.CollectionImage>
            </s.CollectionCards>

            <s.CollectionCards>
              <s.CollectionImage src={d3}></s.CollectionImage>
            </s.CollectionCards>

            <s.CollectionCards>
              <s.CollectionImage src={d4}></s.CollectionImage>
            </s.CollectionCards>

            <s.CollectionCards>
              <s.CollectionImage src={d5}></s.CollectionImage>
            </s.CollectionCards>

            <s.CollectionCards>
              <s.CollectionImage src={d6}></s.CollectionImage>
            </s.CollectionCards>

            <s.CollectionCards>
              <s.CollectionImage src={d7}></s.CollectionImage>
            </s.CollectionCards>

            <s.CollectionCards>
              <s.CollectionImage src={d8}></s.CollectionImage>
            </s.CollectionCards>
          </s.SubHContainer>
        )}

        <s.StickyBarSpacer> </s.StickyBarSpacer>
      </s.SubContainer>
    </ScrollableAnchor>
  );
}

export { CollectionsSection };
